/** @jsxImportSource @emotion/react */
import React from "react";
import { Col, Container, Row } from "react-bootstrap";
import { css } from "@emotion/react";
import { DashboardItem } from "./dashBoard-item";

export function Dashboard({
  confirmed, 
  prev_confirmed, 
  death,
  prev_death,
  hospital,
  prev_hospital,
  critical,
  prev_critical,
}) {
  
  return (
    <Container
      css={css`
        text-align: center;
        background-color: white;
        border-radius: 10px;
        padding-top: 10px;
        padding-bottom: 10px;
        border: 2px solid #000000;

        h2 {
          padding-top: 10px;
          padding-bottom: 10px;
          font-size: 23px;
        }
      `}
    >
      <h2>실시간 확진자 현황</h2>
      <h1
        css={css`
          text-align: center;
          font-size: 13px;
        `}
      >
       질병관리청 홈페이지 기준
      </h1>
      <br/>
      <Row>
        <Col xs={3} md>
          <DashboardItem text="확진자" current={confirmed} prev={prev_confirmed} />
        </Col>
        <Col xs={3} md>
          <DashboardItem text="사망자" current={death} prev={prev_death} />
        </Col>
        <Col xs={3} md>
          <DashboardItem text="신규입원" current={hospital} prev={prev_hospital} />
        </Col>
        <Col xs={3} md>
          <DashboardItem text="위중증" current={critical} prev={prev_critical} />
        </Col>
      </Row>
    </Container>
    
  );
}
