/** @jsxImportSource @emotion/react */
import React from "react";

import { css } from "@emotion/react";
import { formatDiff, numberWithCommas } from "./utils/formatter";

export function DashboardItem(props) {
  const { text, current, prev, diffColor, unit } = props;
  const finalDiffColor = diffColor ? diffColor : "#666666";
  const formattedNumber =
    unit === "percent" ? `${current}%` : numberWithCommas(current);

  return (
    <div
      css={css`
        font-size: 15px;
        position: relative;
      `}
    >
     <p>{text}</p>
      <p
        css={css`
          font-size: 22px;
          font-weight: 500;
          @media (max-width: 576px) {
            font-size: 20px;
          }
        `}
      >
        {formattedNumber}
      </p>
      {prev ? (
        <p
          css={css`
            position: absolute;
            top: 24px;
            width: 100%;
            color: ${finalDiffColor};
          `}
        >
          {formatDiff(current, prev)}
        </p>
      ) : null}
     
    </div>
  );
}
