/** @jsxImportSource @emotion/react */
import React from "react";
import { css } from "@emotion/react";
import { Chart } from "react-google-charts";


export const options = {
  title: " 일 확진자",
  
  legend: { position: "bottom" },
};

export function TotalChart(
    {
        confirmed,
        prev_confirmed,
        prev_confirmed1,
        prev_confirmed2,
        prev_confirmed3,
        prev_confirmed4,
        prev_confirmed5,

    }
) {
    let date = new Date();
    let month = ("0" + (1 + date.getMonth())).slice(-2);
    let day = ("0" + date.getDate()).slice(-2);

    const yesterday = new Date(date);
    yesterday.setDate(date.getDate() - 1);
    let ysMonth = ("0" + (1 + yesterday.getMonth())).slice(-2);
    let ysDay = ("0" + yesterday.getDate()).slice(-2);

    const yesterday1 = new Date(date);
    yesterday1.setDate(date.getDate() - 2);
    let ysMonth1 = ("0" + (1 + yesterday1.getMonth())).slice(-2);
    let ysDay1 = ("0" + yesterday1.getDate()).slice(-2);

    const yesterday2 = new Date(date);
    yesterday2.setDate(date.getDate() - 3);
    let ysMonth2 = ("0" + (1 + yesterday2.getMonth())).slice(-2);
    let ysDay2 = ("0" + yesterday2.getDate()).slice(-2);

    const yesterday3 = new Date(date);
    yesterday3.setDate(date.getDate() - 4);
    let ysMonth3 = ("0" + (1 + yesterday3.getMonth())).slice(-2);
    let ysDay3 = ("0" + yesterday3.getDate()).slice(-2);

    const yesterday4 = new Date(date);
    yesterday4.setDate(date.getDate() - 5);
    let ysMonth4 = ("0" + (1 + yesterday4.getMonth())).slice(-2);
    let ysDay4 = ("0" + yesterday4.getDate()).slice(-2);

    const yesterday5 = new Date(date);
    yesterday5.setDate(date.getDate() - 6);
    let ysMonth5 = ("0" + (1 + yesterday5.getMonth())).slice(-2);
    let ysDay5 = ("0" + yesterday5.getDate()).slice(-2);


    let prevDay5 = ysMonth5 +"."+ysDay5;
    let prevDay4 = ysMonth4 +"."+ysDay4;
    let prevDay3 = ysMonth3 +"."+ysDay3;
    let prevDay2 = ysMonth2 +"."+ysDay2;
    let prevDay1 = ysMonth1 +"."+ysDay1;
    let prevDay = ysMonth +"."+ysDay;
    let today =  month +"."+ day;


    const data = [
        ["날짜", "확진자"],
        [prevDay5, prev_confirmed5],
        [prevDay4, prev_confirmed4],
        [prevDay3, prev_confirmed3],
        [prevDay2, prev_confirmed2],
        [prevDay1, prev_confirmed1],
        [prevDay, prev_confirmed],
        [today, confirmed],
      ];

  return (
    <div
    className="TotalChart"
    css={css`
    text-align: center;
    padding-top: 10px;
    padding-bottom: 10px;
    font-size: 23px;
  `}
    >
    일일 확진자 그래프

    <h2
        css={css`
        text-align: center;
        padding-top: 20px;
        padding-bottom: 10px;
        font-size: 13px;
      `}
      >질병관리청 홈페이지 기준</h2>
    
    <Chart
    css={css`
    max-width: 1000px;
    margin: 0px auto;
    }
  `}
      chartType="LineChart"
      width="100%"
      height="400px"
      data={data}
      options={options}
    />
    </div>
  );
}