/** @jsxImportSource @emotion/react */
import ReactPlayer from 'react-player';
import { css } from "@emotion/react";

export function YoutubeSlide (
    {   
        YoutubeData1,
        YoutubeData2,
        //YoutubeData3,
    }){
    return (
        <div className='YoutubeSlide'>
            <h2
                css={css`
                text-align: center;
                padding-top: 10px;
                padding-bottom: 10px;
                font-size: 23px;
              `}
            >코로나 유튜브</h2>
            <h1 css={css`
        text-align: center;
        padding-top: 10px;
        padding-bottom: 20px;
        font-size: 13px;
      `}> 
      유튜브 코로나 검색 기준 가장 상위 영상을 불러옵니다</h1>
            <div className='player-wrapper'>
                <ReactPlayer
                    css={css`
                    max-width: 600px;
                    height: 100%;
                    margin: 0px auto;
                    }
                  `}

                    className='react-player'
                    url={`https://www.youtube.com/watch?v=${YoutubeData1}`}    // 플레이어 url
                    width='100%'         // 플레이어 크기 (가로)
                    height="400px"       // 플레이어 크기 (세로)
                    playing={false}        // 자동 재생 on
                    muted={false}          // 자동 재생 on
                    controls={true}       // 플레이어 컨트롤 노출 여부
                    light={false}         // 플레이어 모드
                    pip={true}            // pip 모드 설정 여부
                    poster={''}   // 플레이어 초기 포스터 사진
                    //onEnded={() => handleVideo()}  // 플레이어 끝났을 때 이벤트
                />
                <br/>
                <ReactPlayer
                    css={css`
                    max-width: 600px;
                    margin: 0px auto;
                    }
                  `}
                    className='react-player'
                    url={`https://www.youtube.com/watch?v=${YoutubeData2}`}    // 플레이어 url
                    width='100%'         // 플레이어 크기 (가로)
                    height='400px'        // 플레이어 크기 (세로)
                    playing={false}        // 자동 재생 on
                    muted={false}          // 자동 재생 on
                    controls={true}       // 플레이어 컨트롤 노출 여부
                    light={false}         // 플레이어 모드
                    pip={true}            // pip 모드 설정 여부
                    poster={''}   // 플레이어 초기 포스터 사진
                    //onEnded={() => handleVideo()}  // 플레이어 끝났을 때 이벤트
                />
            </div>
        </div>
    )
}
export default YoutubeSlide;